.Toolbar {
    width: 100%;
    height: 56px;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #703B09;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 25px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}
.Toolbar nav {   
    height: 100%;

}
.Logo {
    height: 80%;
}
@media (max-width:499px){
    .DesctopOnly{
        display: none;
    }
}


